<template>
  <div class="content-center">
    <h2>{{ msg }}</h2>
    <div>
      <canvas :width="canvas.width" :height="canvas.height" id="canv"></canvas>
    </div>
    <div>
      <div class="inputs-list" v-for="item in info">
        <div>
          <label for="">
            <span> {{ item.title }}</span>
            <input min="1" max="20" type="number" v-model="item.val" @change="drawChart" />
          </label>
        </div>
      </div>
    </div>
    <button @click="addItem">Add item</button>
    <button @click="downloadJson(info)">Download json</button>
  </div>
</template>
<script>
export default {
  name: "CanvasBurst",
  props: {
    msg: String,
  },
  data() {
    return {
      canvas: {
        width: 1600,
        height: 400,
        lineWidth: 10,
        borderWidth: 50,
      },
      info: [],
      vueCanvas: null,
      message: "Vue + Canvas",
    };
  },
  mounted() {
    const info = `[{"title": "Monday", "val": "5"}, {"title": "Tuesday", "val": "3"}, {"title": "Wadnesday", "val": "8"}, {"title": "Thursday", "val": "5"}, {"title": "friday", "val": "4"}]`;
    this.info = JSON.parse(info);

    this.drawChart();
  },
  methods: {
    drawChart() {
      const c = document.getElementById("canv");
      const ctx = c.getContext("2d");
      this.vueCanvas = ctx;

      const max = Math.max.apply(
        Math,
        this.info.map((o) => o.val)
      );

      const itemsTodraw = this.info.map((item) =>
        this.calculateDrawItem(item, max)
      );

      const offset = parseInt(this.canvas.width / itemsTodraw.length);

      this.vueCanvas.clearRect(0, 0, this.canvas.width, this.canvas.height);

      itemsTodraw.forEach((item, i) => {
        this.drawItem(item, i * offset + parseInt(offset / 2));
      });
    },
    drawItem(item, offset) {
      const grad = this.vueCanvas.createLinearGradient(
        0,
        0,
        0,
        this.canvas.height
      );
      grad.addColorStop(0, "red");
      grad.addColorStop(1, "yellow");

      this.vueCanvas.lineWidth = this.canvas.lineWidth;

      this.vueCanvas.strokeStyle = grad;

      this.vueCanvas.beginPath();
      this.vueCanvas.moveTo(
        offset,
        this.canvas.height - this.canvas.borderWidth
      );

      let targetH =
        parseInt(this.canvas.height) -
        parseInt(
          item.relativeValue *
            (this.canvas.height - this.canvas.borderWidth * 2)
        );

      let H0 = this.canvas.height - this.canvas.borderWidth;
      this.vueCanvas.lineTo(offset, targetH);
      this.vueCanvas.stroke();
    },
    calculateDrawItem(item, max) {
      return {
        absoluteValue: item.val,
        title: item.title,
        relativeValue: item.val / max,
      };
    },
    addItem() {
      this.info.push({ title: "New item", val: "59" });
      this.drawChart();
    },
    download(content, fileName, contentType) {
      const a = document.createElement("a");
      const file = new Blob([content], { type: contentType });
      a.href = URL.createObjectURL(file);
      a.download = fileName;
      a.click();
    },
    downloadJson(jsonData) {
      this.download(JSON.stringify(jsonData), "data.json", "text/plain");
    },
  },
};
</script> 
<style scoped>
canvas {
  border: 1px dotted #ccc;
  max-width: 80vw;
  min-width: 80vw;
}
.inputs-list label {
  width: 80vw;
  border: 1px solid #ccc;
  border-radius: 4px;
  display: block;
  margin: 4px auto;
}
.inputs-list label span {
  width: 200px;
  display: inline-block;
}
.inputs-list label input {
  width: calc(100% - 220px);
  display: inline-block;
}
</style>
